import CloseRoundedIcon from '@mui/icons-material/CloseRounded'
import ErrorIcon from '@mui/icons-material/Error'
import CheckCircleIcon from '@mui/icons-material/CheckCircle';

export default function Modal({handleClose, navigateToList, isValidForm,  messageText, }) {
    return (
        <div id='modalBackground' onClick={handleClose}>
            <div id='modalWrapper'>
                <div id='modalUpper'>
                    {isValidForm? 
                        <CheckCircleIcon id='modalIcon' /> 
                    :
                        <ErrorIcon id='modalIcon' />}
                    <p id='modalText'>{messageText}</p>
                </div>
                {isValidForm?  
                    <button id='modalBtn' onClick={navigateToList}>View profiles</button> 
                : 
                    null}
                <button id='modalBtn' onClick={handleClose}>OK</button>
                <div id='modalClosingWrapper' onClick={handleClose}>
                    <CloseRoundedIcon id='xMarkIcon'/>
                </div>
            </div>
        </div>
    )
}
