import {useEffect} from 'react'
import { Routes, Route } from 'react-router-dom'
import './styles/App.css'
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider'
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs'
import 'dayjs/locale/en'
import Home from './pages/Home'
import ViewEmployees from './pages/ViewEmployees'
import NewEmployee from './pages/NewEmployee'
import Page404 from './pages/Page404'
import useStore from './store/store'
import LightModeRoundedIcon from '@mui/icons-material/LightModeRounded'
import NightlightRoundRoundedIcon from '@mui/icons-material/NightlightRoundRounded'

function App() {
  // get theme state from the store
  const theme = useStore(state => state.theme)
  // set theme from the store
  const setTheme = useStore(state => state.setTheme)

  //handle theme button
  const toggleTheme = (e) => {
    e.preventDefault()
    if (theme === 'light') {
      setTheme('dark')
    } else {
      setTheme('light')
    }
  }
  // state console
  useEffect(() => {
    document.body.className = theme === 'dark'? 'darkTheme' : null
  }, [theme])
  
  return (
    <>
      <LocalizationProvider dateAdapter={AdapterDayjs} adapterLocale='en'>
        <main>
            <Routes>
              <Route path="/" element={<Home />} />
              <Route path="/employees/list" element={<ViewEmployees />} />
              <Route path="/employees/new" element={<NewEmployee />} />
              <Route path="*" element={<Page404/>} />
            </Routes>
            <div id='themeToggleWrapper' onClick={toggleTheme}>
              <NightlightRoundRoundedIcon style= {{color: theme === 'light'? 'var(--dark-color)' : 'var(--primary-color)'}}/> <LightModeRoundedIcon style= {{color: theme === 'dark'? 'var(--dark-color)' : 'var(--primary-color)'}}/>
            </div>
        </main>
      </LocalizationProvider>
    </>
  )
}

export default App
